.container {
  max-width: 1300px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
  margin-top: 60px;
  margin-bottom: 140px;
}

.title {
  font-weight: 700;
  font-size: 55px;
  line-height: 105.5%;
  color: #000000;
  margin-bottom: 70px;
  text-align: center;
}

.img_box {
  width: 268px;
}

.card_title {
  font-weight: 700;
  font-size: 25px;
  line-height: 30px;
  color: #000000;
  margin-top: 16px;
}

.card_text {
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  margin-top: 9px;
  margin-bottom: 27px;
  color: #000000;
}
.card_text::before {
  content: "";
  width: 18px;
  height: 24px;
  display: inline-block;
  background-repeat: no-repeat;
  background-image: url("../imgs/select.svg");
  vertical-align: middle;
  margin-right: 5px;
}

.card_btn {
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
  color: #ffffff;
  padding: 9px 50px;
  border: none;
  background: #ca0200;
  box-shadow: 2px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 21.5px;
}


.cards{
  display: flex;
  align-items: center;
  flex-direction: column;
}

.card_box{
  display: flex;
  align-items: center;
  justify-content: space-around;
}