html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}
body {
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}
ul,
ol {
  list-style: none;
  margin: 0;
  padding: 0;
}
a {
  display: inline-block;
  text-decoration: none;
}
img {
  width: 100%;
  height: auto;
  border-radius: 27px;
}
.container {
  max-width: 1300px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
}

::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-thumb {
  background: #1c1c1c;
  border-radius: 5px;
}

::-webkit-scrollbar-thumb:hover {
  background: #272727;
}

.logo_box {
  width: 206px;
  >a>img{
    border-radius: 0;
  }
}

.nav_link {
  font-weight: 500;
  font-size: 20px;
  line-height: 26px;
  color: #000000;
}

.container{
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 30px;
}

.buttons{
  display: flex;
  align-items: center;
  gap: 21px;
}

.nav{
  display: flex;
  align-items: center;
  gap: 34px;

}

.search,.basket,.account{
  background-color: transparent;
  border: none;
  background-repeat: no-repeat;
  background-position: center;  
  width: 30px;
  height: 30px;
  cursor: pointer;
}

.search{
  background-image: url('../imgs/search.svg');
}

.basket{
  background-image: url('../imgs/basket.svg');
}

.account{
  background-image: url('../imgs/account.svg');
}