
.container {
  max-width: 1300px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
}



.title {
  font-weight: 700;
  font-size: 59px;
  line-height: 115%;
  color: #000000;
  max-width: 600px;
} 
.btn { 
  font-weight: 600;
  font-size: 30px;
  line-height: 100%;
  color: #ca0200;
  padding: 8px 49px;
  border: 2px dashed #ca0200;
  background-color: transparent;
  margin-top: 31px;
}

.text {
  font-weight: 500;
  font-size: 20px;
  line-height: 115%;
  color: #000000;
  margin-top: 30px;
  margin-bottom: 36px;
  width: 438px;
}

.btn2 {
  font-weight: 600;
  font-size: 34px;
  line-height: 115%;
  color: #ffffff;
  padding: 15px 30px;
  border: none;
  width: 283px;
  background: #ca0200;
  box-shadow: 2px 5px 4px rgba(0, 0, 0, 0.25);
  border-radius: 34.5px;
}

.container{
  background-repeat: no-repeat;
  background-image: url('../imgs/hero_img.png');
  background-position-x: right;
  background-size: contain;
  background-size: 900px auto;
  padding-top: 100px;
  padding-bottom: 190px;
}
