.container {
  max-width: 1300px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
  margin-top: 60px;
  margin-bottom: 140px;
}

.img_box {
  width: 524px;
}

.title {
  font-weight: 700;
  font-size: 100px;
  line-height: 105.5%;
  color: #000000;
  width: 618px;
}

.text {
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  margin-top: 21px;
  margin-bottom: 50px;
  color: rgba(0, 0, 0, 0.71);
  width: 513px;
}

.btn {
  font-weight: 600;
  font-size: 34px;
  line-height: 115%;
  color: #ffffff;
  background: #ca0200;
  box-shadow: 3px 5px 4px rgba(0, 0, 0, 0.25);
  border-radius: 34.5px;
  border: none;
  padding: 15px 30px;
}

.container{
  display: flex;

  align-items: center;
  justify-content: space-around;
}