.container {
  max-width: 1358px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 60px;
  margin-bottom: 140px;
}

.img1_box {
  width: 211px;
  height: 211px;
  >img{
    height: 211px;
    border-radius: 0;
  }
}

.online_main {
  padding: 90px 85px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 739px;
  gap: 94px;
  background-repeat: no-repeat;
  background-image: url("../imgs/online2.jpg");
  height: 211px;
  background-size: cover;
}

.online_mainText {
  font-weight: 400;
  font-size: 25px;
  line-height: 115%;
  color: #ffffff;
}

.online_btn {
  background-color: #000;
  padding: 90px 150px;
  font-weight: 400;
  font-size: 25px;
  line-height: 115%;
  color: #ffffff;
  height: 211px;
  cursor: pointer;
}
.online_btn::after{
  content: '';
  background-repeat: no-repeat;
  background-image: url('../imgs/arrow.svg');
  width: 28px;
  height: 15px;
  display: inline-block;
  background-position: center;
  margin-left: 40px;
}

.container{
  display: flex;
  align-items: center;
  justify-content: center;
}