.container {
  max-width: 1300px;
  margin-left: auto;
  margin-right: auto;
  padding: 67px 153px;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}

.footer {
  background-color: #000;
}

.logo_box {
  width: 206px;
  >a>img{
    border-radius: 0;
  }
}

.links {
  > h3 {
    font-weight: 600;
    font-size: 22px;
    line-height: 26px;
    margin-bottom: 14px;
    color: #ffffff;
  }
  > ul > li > p >a{
    font-weight: 500;
    font-size: 18px;
    line-height: 31px;
    color: #ffffff;
  }
}
