.container {
  max-width: 1300px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
  margin-top: 60px;
  margin-bottom: 140px;
}

.title {
  font-weight: 700;
  font-size: 55px;
  line-height: 105.5%;
  color: #000000;
  text-align: center;
}

.text {
  font-weight: 500;
  font-size: 20px;
  line-height: 105.5%;
  margin-top: 14px;
  margin-bottom: 37px;
  text-align: center;
  color: #000000;
}

.img1 {
  width: 623px;
}

.img2,
.img3 {
  width: 510px;
}

.img3 {
  margin-top: 20px;
}

.ims_box {
  display: flex;
  align-items: center;
  justify-content: space-around;
  margin-bottom: 100px;
}

.subscribe {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: #a3b7f5;
  box-shadow: 4px 7px 4px rgba(0, 0, 0, 0.25);
  border-radius: 33px;
  padding: 20px;
  padding-left: 60px;
  height: 365px;
  > div {
    > h2 {
      font-weight: 700;
      font-size: 30px;
      line-height: 105.5%;
      color: #000000;
      width: 510px;
      > span {
        color: #ca0200;
      }
    }
    > input {
      background: #ffffff;
      border-radius: 9px;
      padding: 10px 40px;
      margin-top: 40px;
      width: 602px;
      border: none;
    }
    > button {
      font-weight: 600;
      font-size: 18px;
      line-height: 115%;
      color: #ffffff;
      padding: 9px 38px;
      background: #ca0200;
      box-shadow: 2px 3px 4px rgba(0, 0, 0, 0.25);
      border-radius: 34.5px;
      border: none;
      display: block;
      margin-top: 50px;
    }
  }
  >div:nth-child(2){
    width: 344px;
    margin-top: -75px;
    margin-right: -23.2px;
  }
}
